import Vue from 'vue'
// directives
// 

// v-tip
//  `v-tip`()
import tip from './tip'
Vue.directive(tip.name, tip.func) // v-tip

// v-modal
//  `v-modal`(  v-model )
import modal from './modal'
Vue.directive(modal.name, modal.func) // v-modal

// v-lazy
//  /
// 
import lazy from './lazy'
Vue.directive(lazy.name, lazy.func) // v-lazy

export default {}
