<template>
  <div>
    <div
      v-for="(item, index) in Paths"
      :key="index"
      class="container overflow-auto px-0"
    >
      <div
        v-if="item.pathInfo.name && item.pathInfo.path"
        class="btn btn-primary text-truncate d-flex justify-content-between align-items-center px-1"
        role="button"
        :class="{ active: $route.path == item.pathInfo.path }"
        @click.right="mouseRightClick(event, item)"
        @dblclick="$router.replace(item.pathInfo)"
      >
        <router-link
          class="text-white text-truncate text-decoration-none text-center"
          style="min-width: 3rem;"
          :to="item.pathInfo.path"
        >
          <font class="px-1 px-1">
            {{ item.pathInfo.name }}
          </font>
        </router-link>
        <i
          class="fas fa-times-circle px-1"
          style="font-size: 0.8em"
          @click.stop="closeTab(item, 'closeIco')"
        />
      </div>
    </div>
    <!--  -->
    <ul
      v-show="isShow"
      class="list-group p-1"
      style="position: absolute; z-index: 1050"
      :style="{ 'top': top, 'left': left }"
    >
      <li
        class="list-group-item list-group-item-action btn p-1"
        role="button"
        @mousedown="closeTab(RightClickItem, 'rigthClick')"
      >
        <font class="text-truncate text-decoration-none text-center text-muted d-inline-block">
          Close
        </font>
      </li>
      <li
        class="list-group-item list-group-item-action btn p-1"
        role="button"
        @mousedown="closeOthers(RightClickItem)"
      >
        <font
          class="text-truncate text-decoration-none text-center text-muted d-inline-block"
        >
          Close Others
        </font>
      </li>
      <li
        class="list-group-item list-group-item-action btn p-1"
        role="button"
        @mousedown="closeAll"
      >
        <font
          class="text-truncate text-decoration-none text-center text-muted d-inline-block"
        >
          Closs All
        </font>
      </li>
    </ul>
  </div>
</template>

<script>
import router from "@/router/index";

export default {
  name: "NavTabs",
  data() {
    return {
      pathList: [],
      maxHistory: 0,
      Paths: [],
      RightClickItem: null,
      top: "0px",
      left: "0px",
      isShow: false
    };
  },
  watch: {
    isShow: function(newValue) {
      newValue ? this.bindHideEvents() : this.unbindHideEvents();
    },
    "pathList.length": function() {
      sessionStorage.System_NavTabs_PathList = JSON.stringify(this.pathList, null, ['pathInfo', 'index']);
    }
  },
  created() {
    router.afterEach((to, from) => {
      if (to && to.path != "/" && to != from) {
        let flag = false;
        for (const item of this.pathList) {
          if (item.pathInfo.path == to.path) {
            item.index = ++this.maxHistory;
            flag = true;
            break;
          }
        }
        if (!flag)
          this.pathList.push({ pathInfo: to, index: ++this.maxHistory });
      }
    });
    this.Paths = this.pathList;
    this.$nextTick(() => {
      //  sessionStorage 
      if (sessionStorage.System_NavTabs_PathList)
        this.pathList.push(
          ...(JSON.parse(sessionStorage.System_NavTabs_PathList) || [])
        );
    });
  },
  methods: {
    removePath: function(url) {
      for (let n = 0; n < this.pathList.length; n++) {
        if (this.pathList[n].pathInfo.path == url) {
          this.pathList.splice(n, 1);
          break;
        }
      }
    },
    // 
    closeTab: function(willClose, type = "") {
      if (!(willClose && willClose.pathInfo)) return;
      // 
      if (type == "rigthClick") this.rightContextHiden();
      this.removePath(willClose.pathInfo.path);
      // 
      if (this.$route.path != willClose.pathInfo.path) return;
      // 
      if (this.pathList.length == 0) {
        this.$router.push("/");
      } else {
        let arr = this.pathList.slice(0);
        // a.index - b.index 
        let item = Object.assign(
          {},
          arr.sort((a, b) => a.index - b.index)[arr.length - 1].pathInfo
        );
        this.$router.push(item.path);
      }
    },
    // 
    closeOthers: function(item) {
      if (!(item && item.pathInfo)) return;
      // pathList = [] vue 
      this.maxHistory = 0;
      this.pathList.splice(0, this.pathList.length);
      this.pathList.push({ pathInfo: item.pathInfo, index: ++this.maxHistory });
      // 
      if (this.$route.path == item.pathInfo.path) return;
      // 
      this.$router.push(item.pathInfo.path);
    },
    // 
    closeAll: function() {
      // pathList = [] vue 
      this.pathList.splice(0, this.pathList.length);
      this.maxHistory = 0;
      this.$router.push("/");
    },
    // 
    bindHideEvents() {
      document.addEventListener("mousedown", this.rightContextHiden);
      document.addEventListener("mousewheel", this.rightContextHiden);
    },
    // 
    unbindHideEvents() {
      document.removeEventListener("mousedown", this.rightContextHiden);
      document.removeEventListener("mousewheel", this.rightContextHiden);
    },
    // 
    mouseRightClick: function(event, item) {
      // 
      document.oncontextmenu = new Function("return false;");
      const e = event || window.event;
      this.top =
        (e.pageY ||
          e.clientY +
            (document.documentElement.scrollTop || document.body.scrollTop)) +
        "px";
      this.left =
        (e.pageX ||
          e.clientX +
            (document.documentElement.scrollLeft || document.body.scrollLeft)) +
        "px";
      this.isShow = true;
      this.RightClickItem = item;
    },
    // 
    rightContextHiden: function() {
      this.isShow = false;
      this.RightClickItem = null;
      // 
      document.oncontextmenu = new Function("return true;");
    }
  }
};
</script>
